    },
    {
      id: 'marker-8',
      position: { yaw: '11deg', pitch: '-12deg' },
      url: 'images/room1.jpg',
      html: '<div class="go-to-page">通往阳台<img src="images/new_spotd1.png" /></div>',
      size: { width: 32, height: 32 },
      changeScene: true,
    }
