      url: 'images/angra.jpg',
      html: '<div class="go-to-page imggif">通往小镇<img src="images/new_spotd1.gif" /></div>',
      size: { width: 32, height: 32 },
      changeScene: true,
    }
  ]
}

const animatedValues = {
  pitch: { start: -Math.PI / 2, end: 0 },
  yaw: { start: Math.PI / 2, end: 0 },
  zoom: { start: 0, end: 12 },
  maxFov: { start: 130, end: 90 },
  fisheye: { start: 2, end: 0 },
};

const changeScene = (url: string) => {
  viewer.setPanorama(url, {
    speed: '20rpm',
    showLoader: false,
    position: { yaw: 0, pitch: 0 },
  }).then(() => {
    markersPlugin.clearMarkers()
    markers[url].forEach((item: any) => {
      markersPlugin.addMarker(item);
    });
  });
}

onMounted(() => {
  viewer = new Viewer({
    container: 'viewer',
    panorama: 'images/angra.jpg',
    caption: 'Parc national du Mercantour <b>&copy; Damien Sorel</b>',
    // defaultPitch: animatedValues.pitch.start,
    // defaultYaw: animatedValues.yaw.start,
    // defaultZoomLvl: animatedValues.zoom.start,
    // maxFov: animatedValues.maxFov.start,
    // fisheye: animatedValues.fisheye.start,
    mousemove: false,
    mousewheel: false,
    loadingImg: '',
    loadingTxt: '加载中',
    // touchmoveTwoFingers: true, // 双指缩放
    // mousewheelCtrlKey: true, // ctrl + 鼠标滚轮缩放
    // navbar: [ // 底部导航，缩放、重置、全屏
    //   'zoom',
    //   {
    //     title: 'Rerun animation',
    //     content: '🔄',
    //     onClick: reset,
    //   },
    //   'caption',
    //   'fullscreen',
    // ],
    plugins: [
      // [AutorotatePlugin, {
      //   autostartDelay: 1000,
      //   autorotatePitch: '5deg',
      // }],
      [MarkersPlugin, {
        markers: [
          // {
          //   id: 'marker-1',
          //   position: { pitch: 0.11, yaw: -0.35 },
          //   image: 'images/logo.png',
          //   size: { width: 32, height: 32 },
          //   // anchor: 'bottom center',
          //   // tooltip: 'Default scaling',
          //   hoverScale: false,

          // },
          // {
          //   id: 'marker-2',
          //   position: { pitch: 0.32, yaw: 0.11 },
          //   image: 'images/logo.png',
          //   size: { width: 32, height: 32 },
          //   // anchor: 'bottom center',
          //   // tooltip: 'Disable scaling',
          //   hoverScale: false,
          // },
          // {
          //   id: 'marker-3',
          //   position: { pitch: -0.05, yaw: 0.04 },
          //   image: 'images/logo.png',
          //   size: { width: 32, height: 32 },
          //   // anchor: 'bottom center',
          //   // tooltip: 'Custom scaling',
          //   // hoverScale: { amount: 3, easing: 'ease-in-out', duration: 1000 }, // mark缩放
          //   hoverScale: false,
          // },
        ],
      }],
    ],
  });

  let isInit = true;

  // setup timer for automatic animation on startup
  viewer.addEventListener('ready', () => {
    if (isInit) {
      intro(animatedValues.pitch.end, animatedValues.pitch.end);
    }
    markersPlugin = viewer.getPlugin(MarkersPlugin)
    markers['images/angra.jpg'].forEach((item: any) => {
      markersPlugin.addMarker(item);
    });
    // markersPlugin.addMarker({
    //   id: 'marker-1',
    //   position: { pitch: 0.11, yaw: -0.35 },
    //   size: { width: 32, height: 32 },
    //   hoverScale: false,
    // })
    // changeScene('images/angra.jpg', true)
    // markersPlugin.markers['marker-1'].element.addEventListener('click', () => {
    //   console.log(2222)
    // })

    // markersPlugin.hideMarker('marker-1')
    setTimeout(() => {
      // const autorotate = viewer.getPlugin(AutorotatePlugin)
      // autorotate.stop()
      // autorotate.start()
      // viewer.setPanorama('images/room1.jpg', {
      //   speed: '20rpm',
      //   showLoader: false,
      //   position: { yaw: 0, pitch: 0 },
      // });
      // markersPlugin.showMarker('marker-1')
      // // markersPlugin.gotoMarker('marker-3')
      // markersPlugin.showMarker('marker-1')
    }, 5000);

    markersPlugin.addEventListener('select-marker', ({ marker }: any) => {
      if(marker.config.changeScene) {
        changeScene(marker.config.url)
      } else {
        alertText.value = marker.config.text
      }
    });
  }, { once: true });



  // launch animation to clicked point
  // viewer.addEventListener('click', ({ data }) => {
  //   if (isInit) {
  //     intro(data.pitch, data.yaw);
  //     // viewer.setPanorama('images/room1.jpg');
  //   }
  // });

  // perform the intro animation
  function intro(pitch: any, yaw: any) {
    isInit = false;
    viewer.navbar.hide();

    new utils.Animation({
      properties: {
        ...animatedValues,
        pitch: { start: animatedValues.pitch.start, end: pitch },
        yaw: { start: animatedValues.yaw.start, end: yaw },
      },
      duration: 2500,
      easing: 'inOutQuad',
      onTick: (properties) => {
        viewer.setOptions({
          fisheye: properties.fisheye,
          maxFov: properties.maxFov,
        });
        viewer.rotate({ yaw: properties.yaw, pitch: properties.pitch });
        viewer.zoom(properties.zoom);
      },
    }).then(() => {
      // viewer.navbar.show();
      viewer.setOptions({
        mousemove: true,
        mousewheel: true,
      });
    });
  }

  // perform the reverse animation
  function reset() {
    isInit = true;
    viewer.navbar.hide();
    viewer.setOptions({
      mousemove: false,
      mousewheel: false,
    });

    new utils.Animation({
      properties: {
        pitch: { start: viewer.getPosition().pitch, end: animatedValues.pitch.start },
        yaw: { start: viewer.getPosition().yaw, end: animatedValues.yaw.start },
        zoom: { start: viewer.getZoomLevel(), end: animatedValues.zoom.start },
        maxFov: { start: animatedValues.maxFov.end, end: animatedValues.maxFov.start },
        fisheye: { start: animatedValues.fisheye.end, end: animatedValues.fisheye.start }
      },
      duration: 1500,
      easing: 'inOutQuad',
      onTick: (properties) => {
        viewer.setOptions({
          fisheye: properties.fisheye,
          maxFov: properties.maxFov,
        });
        viewer.rotate({ yaw: properties.yaw, pitch: properties.pitch });
        viewer.zoom(properties.zoom);
      },
    });
  }
})
